import cuCustom from '@/colorui/components/cu-custom.vue'
import asyncSwitch from "@/components/helang-asyncSwitch/helang-asyncSwitch.vue"
import mpopup from '../../../../components/xuan-popup/xuan-popup.vue'
export default {
	data() {
		return {
			// 收货地址信息
			siteMsg: {
				name:'',
				phone:'',
				dormitory:'',
				dormitory_no:'',
				address:'',
				checked1:true
			}
		}
	},
	methods: {
		//地图选择地址
		chooseLocation(){
			uni.chooseLocation({
				success: (data)=> {
					this.addressData.addressName = data.name;
					this.addressData.address = data.name;
				}
			})
		},
			
		change1() {
			if(this.siteMsg.checked1) {
				this.siteMsg.checked1 = false
			} else {
				this.siteMsg.checked1 = true
			}
		},
		addSite() {
			// console.log(this.siteMsg.checked1)
			if(this.siteMsg.name == '') {
				this.$refs.mpopup.open({
				    type:'err',
				    content:'收货人姓名不能为空',
				    timeout:1000,
				    isClick:false
				});
				return
			} else if(this.siteMsg.phone == '' || parseInt(this.siteMsg.phone) != this.siteMsg.phone) {
				this.$refs.mpopup.open({
				    type:'err',
				    content:'收货人电话不能为空,并且必须是数字',
				    timeout:1000,
				    isClick:false
				});
				return
			} else if(this.siteMsg.dormitory == '') {
				this.$refs.mpopup.open({
				    type:'err',
				    content:'收货人宿舍楼不能为空',
				    timeout:1000,
				    isClick:false
				});
				return
			} else if(this.siteMsg.dormitory_no == '') {
				this.$refs.mpopup.open({
				    type:'err',
				    content:'收货人宿舍号不能为空',
				    timeout:1000,
				    isClick:false
				});
				return
			}
			let id = uni.getStorageSync('user_id')
			uni.request({
				// 接口
				url: 'http://47.104.29.236:9999/api/v1/addSite',
				// 请求方式
				method: 'POST',
				// 携带的数据
				data: {addUsers:this.siteMsg,id:id},
				// 请求成功
				success: res => {
					// console.log(res)
					if(res.data.ok == 1) {
						this.$refs.mpopup.open({
						    type:'success',
						    content:'添加地址成功',
						    timeout:2000,
						});
						setTimeout(()=> {
							// 返回地址页面
							uni.navigateTo({
								url:'../../xy-site/site',
								success:res =>{},
								fail:()=>{},
								complete:()=>{}
							})
						},2000)
					}
				},
				// 请求失败
				fail: () => {},
				complete: () => {}
			})
			
			// uni.navigateTo({
			// 	url:'../../xy-site/site',
			// 	success:res =>{},
			// 	fail:()=>{},
			// 	complete:()=>{}
			// })
		},
		
	},
	components: {
		cuCustom,
		"async-switch":asyncSwitch,
		mpopup
	},
	created() {
		
	}
}
